import React, { Component } from 'react';
import me from '../media/me.jpg';
import $ from 'jquery';

class About extends Component {
    componentDidMount () {

        var skillsDiv = $('#skills');

        $(window).on('scroll', function(){
            var winT = $(window).scrollTop(),
                winH = $(window).height(),
                skillsT = skillsDiv.offset().top;

        if(winT + winH  > skillsT){
            $('.skillbar').each(function(){
            $(this).find('.skillbar-bar').animate({
                width:$(this).attr('data-percent')
            },4000);
            });
        }
        });
    }
    render(){
        return(
            <section className="container about" id="about">
                <h1 className="section-heading">A little bit about me</h1>
                <div className="row">
    
                    <article className="margin-bottom col l6 m6 s12">
                        <section className="about-me">
                            <div className="me-wrapper">
                                <img src={ me } className="me" alt="me" />
                            </div>
                            <h1>Aleksi Svaridze</h1>
                            <p>I'm a Front end developer from Tbilisi.
                                 I can convert any .psd or .ai files to html codes.
                                 I work as a freelancer.
                                 During the freelance I prepared several commercial sites,
                                 which are presented below. </p>
                        </section>
                    </article>
    
                    <article className="col l6 m6 s12">
                        <section className="about-skills">
                            <div id="skills">
                                <div className="skillbar clearfix " data-percent="75%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Html5</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">75%</div>
                                </div>

                                <div className="skillbar clearfix " data-percent="70%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Css3</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">70%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="50%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Sass</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">50%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="60%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Materialize</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">60%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="65%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Bootstrap</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">65%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="50%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Javascript</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">50%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="40%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>jQuery</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">40%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="40%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>ReactJs</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">40%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="45%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>WordPress</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">45%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="30%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>PhotoShop</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">30%</div>
                                </div>

                                <div className="skillbar clearfix" data-percent="30%">
                                    <div className="skillbar-title" style={{background: '#262626' }}><span>Illustrator</span></div>
                                    <div className="skillbar-bar" style={{background: '#262626' }}></div>
                                    <div className="skill-bar-percent">30%</div>
                                </div>
                            </div>
    
                        </section>
                    </article>
    
                </div>
            </section>
        )
    }
    
}

export default About;