import React, { Component } from 'react';

class Contact extends Component {
    state = {
        contactItems: [
            { id: 1, con_icon: 'mail',con_url: 'mailto:aleksisvaridze@gmail.com',  con_text: 'Gmail',  con_account: 'aleksisvaridze@gmail.com'},
            { id: 2, con_icon: 'chat',con_url: 'mailto:aleksisvaridze@outlook.com',con_text: 'Outlook',con_account: 'aleksisvaridze@outlook.com'},
            { id: 3, con_icon: 'call',con_url: '#!',                               con_text: 'Mobile', con_account: '593 53 90 95'}
        ]
    }
    render() {
        return(
            <section className="contact" id="contact">
                <h1 className="section-heading">Contact</h1>
                <div className="container">
                    <div className="row">
                        <div className="col s12 m12 contact-items">
                            { this.state.contactItems.map( item => {
                                return (
                                    <div className="card" key={ item.id }>
                                        <div className="card-action">
                                            <span className="card-title"><i className="material-icons margin-right">{item.con_icon}</i> { item.con_text }</span>
                                            <a href={ item.con_url }>{ item.con_account }</a>
                                        </div>
                                    </div>
                                )
                                
                            } )
                            }
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}

export default Contact;