import React, { Component } from 'react';


class Portfolio extends Component {
    state = {
            projectsObject : [
                { id: 1, url: './assets/images/1-1.jpg', desc: 'Karizi.ge', github: 'https://github.com/AleksiSvaridze/karizi.ge', server: 'http://karizi.ge/'},
                { id: 2, url: './assets/images/2-2.jpg', desc: 'Template', github: 'https://github.com/GeoLabJuniors/howto-front', server: 'http://tbilisi.atwebpages.com/howto-front/'},
                { id: 3, url: './assets/images/3-3.jpg', desc: 'KateTravel.ge', github: 'https://github.com/AleksiSvaridze/Guest', server: 'http://tbilisi.atwebpages.com/Guest/index.html'},
                { id: 4, url: './assets/images/4-4.jpg', desc: 'Template', github: 'https://github.com/AleksiSvaridze/auto', server: 'http://tbilisi.atwebpages.com/auto/'},
                { id: 5, url: './assets/images/5-5.jpg', desc: 'CarsForRent.ge', github: '#', server: 'http://carsforrent.ge/'},
                { id: 6, url: './assets/images/6-6.jpg', desc: 'Template', github: 'https://github.com/AleksiSvaridze/Stikers_v2', server: 'http://tbilisi.atwebpages.com/Stikers_v2/'},
                { id: 7, url: './assets/images/7-7.jpg', desc: 'Template', github: 'https://github.com/AleksiSvaridze/Art-Gallery', server: 'http://tbilisi.atwebpages.com/Art-Gallery/'},
                { id: 8, url: './assets/images/8-8.jpg', desc: 'New Portfolio', github: 'https://github.com/AleksiSvaridze/aleksisvaridze.github.io', server: 'https://aleksisvaridze.github.io/'},
                { id: 9, url: './assets/images/9-9.jpg', desc: 'Old Portfolio', github: 'https://github.com/AleksiSvaridze/portfolio', server: 'http://tbilisi.atwebpages.com/aleksisvaridze.github.io/'},
            ]
        };

    render(){
        return(
            <section className="portfolio" id="portfolio">
            <h1 className="section-heading">My Portfolio</h1>    
                <div className="container">
                    <h4 className="h4">All works</h4>
                    <div className="row">
                    { this.state.projectsObject.map( project => {
                        return(
                            <div className="col s12 m6 l4" key={ project.id }>
                                <div className="card">
                                    <div className="card-image">
                                        <img src={ project.url } alt="project-images" />
                                    </div>
        
                                    <div className="card-content">
                                        <p>{ project.desc }</p>
                                    </div>
        
                                    <div className="card-action"> 
                                        <a href={ project.github } rel="noreferrer noopener" target="_blank">Github</a>
                                        <a href={ project.server } rel="noreferrer noopener" target="_blank">Server</a>
                                    </div>
                                </div>
                            </div>
                        )
                } )

            }

                    </div>
                </div>
            </section> 
        )
    }
}
export default Portfolio;