import React, { Component } from 'react';
import $ from 'jquery';

class Header extends Component {
    componentDidMount = () => {
        $('.mobile-menu').on('click', function (e) {
            $('.mob-modal').toggleClass('show');
            e.stopPropagation();
        });
        $(window).on('click', function (e) {           
            $('.mob-modal').removeClass('show');
        })
    }
    render() {
        return(
            <header className="main-header" id="header">            
                <nav className="container">
                    <div className="nav-wrapper">
                        <a href="#home" className="brand-logo scroll">Aleksi Svaridze</a>
                        <ul id="nav-mobile" className="right hide-on-med-and-down">
                            <li><a href="#home" className="scroll">Home</a></li>
                            <li><a href="#about" className="scroll">About</a></li>
                            <li><a href="#portfolio" className="scroll">Portfolio</a></li>
                            <li><a href="#contact" className="scroll">Contact</a></li>
                        </ul>
                        <div className="right mobile-menu">
                            <i className="material-icons">menu</i>
                        </div>
                        <div className="mob-modal">
                            <div className="mob-modal-content">
                                <a href="#home" className="scroll">Home</a>
                                <a href="#about" className="scroll">About</a>
                                <a href="#portfolio" className="scroll">Portfolio</a>
                                <a href="#contact" className="scroll">Contact</a>
                            </div>
                        </div>
                    </div>
                </nav>       
            </header>
        )
    }
}

export default Header;