import React from 'react';
import Design from '../media/Design.mp4';

const Hero = () => {
    return(
        <div className="hero" id="home">
            <video className="bg-video" preload="auto" autoPlay={ true } loop="loop" muted="muted">
                <source src={ Design } type="video/mp4" />
            </video>
            <h1>Welcome</h1>
            <h4>to my portfolio</h4>
            
            <a href="#about" className="scroll">
                <i className="material-icons">filter_list</i>
            </a>
        </div>
    )
} 

export default Hero;