import React, { Component } from 'react';
import Header from './components/header';
import Hero from './components/hero';
import About from './components/about';
import Portfolio from './components/portfolio';
import Current from './components/current';
import Contact from './components/contact';
import Footer from './components/footer';
import $ from 'jquery';

class App extends Component {
  componentDidMount () {

    $(document).ready(function() {
 
      var scrollLink = $('.scroll');
      
      scrollLink.click(function(e) {
        e.preventDefault();
        $('body,html').animate({
          scrollTop: $(this.hash).offset().top
        }, 500);
      });
      
      
      $(window).scroll(function() {
        var scrollBarLocation = $(this).scrollTop();
        scrollLink.each(function() {
          var divOffset = $(this.hash).offset().top
      
          if ( divOffset <= scrollBarLocation ) {
            $(this).parent().addClass('current');
            $(this).parent().siblings().removeClass('current');
          }
        });
      });  // Scroll with slow effect

    var scrollTop = $(".scrollTop");

    $(window).scroll(function() {
        var topPos = $(this).scrollTop();
    if (topPos > 100) {
          $(scrollTop).css("opacity", "1");
        } else {
          $(scrollTop).css("opacity", "0");
        }
    });  // Scroll Up with slow&fade effect

  }); // Function Ready

    
}; // componentDidMount

  state = {
    currentProjects : [
        { id: 1, url: './assets/images/3-3.jpg', desc: 'KateTravel.ge', github: 'https://github.com/AleksiSvaridze/Guest', server: 'http://tbilisi.atwebpages.com/Guest/index.html'},
        { id: 2, url: './assets/images/8-8.jpg', desc: 'New Portfolio', github: 'https://github.com/AleksiSvaridze/aleksisvaridze.github.io', server: 'https://aleksisvaridze.github.io/'},
        { id: 3, url: './assets/images/7-7.jpg', desc: 'Template', github: 'https://github.com/AleksiSvaridze/Art-Gallery', server: 'http://tbilisi.atwebpages.com/Art-Gallery/'},
    ]
};
  render() {
    return (
      <div className="App">
        <Header />
        <Hero />
        <About />
        <Portfolio />
        <Current currentProjects={ this.state.currentProjects } />
        <Contact />
        <Footer />
        <div className="scrollTop"> 
          <a href="#home" className="scroll">
            <i className="material-icons">keyboard_arrow_up</i>
          </a>
        </div>
      </div>
    );
  }
}

export default App;